var graceChecker = require("../../graceUI/graceChecker.js");
import LongButton from '../../components/my-components/LongButton.vue';
let requesting = false;
let canLogin = false; //可否登录 
let sessionId = ''
let DataFrom = {}
export default {
	components: {
		LongButton
	},
	data() {
		return {
			openEye: false,
			userId: '',
			password: '',
			idError: false, //用户信息错误
		}
	},
	computed: {
		disableButton() {
			this.idError = false;
			return !this.userId || !this.password;
		}
	},
	onLoad(option) {
		// uni.clearStorage();//清空缓存
		let that=this;
		DataFrom = this.Cacher.getData(option.from) || {}; //获取页面传参//如果没有from就说明是刚进入小程序
		this.initPage();
		if (!DataFrom.from) {} else {}
		uni.getProvider({
			service: 'oauth',
			success: function(res) {
				if (~res.provider.indexOf('weixin')) {
					uni.login({
						provider: 'weixin',
						success: function(loginRes) {
							console.log('weixin>>>', loginRes);
							that.Request('wechatLogin', {
								code: loginRes.code
							}).then(res => {
								if (res.error == 0) {}
							}).catch(res => {
								console.log('res》》》', res)
							})
						}
					});
				}
			}
		});
	},
	mounted() {
		this.closePageLoading()
	},
	methods: {
		clickButton() {
			console.log('object')
		},
		initPage() {
			this.openEye = false;
			canLogin = false;
			this.userId = 'yilianxinpin';
			this.password = 'Qm8xn4KVBMc0Wd70';
			this.idError = false;
		},
		getUserId(val) {
			this.userId = val.detail;
		},
		getPassWord(val) {
			this.password = val.detail;
		},
		clearInput(key) {
			this[key] = '';
		},
		clickPWIcon() { //切换加密显示与明文显示
			this.openEye = !this.openEye;
		},
		loginWithWx: function() {
			this.closePageLoading();
			this.Toast('当前微信暂未绑定任何管理员账号');
		},
		loginNow(e) { //点击登录
			if (!requesting) { //函数节流
				requesting = true; //是否正在请求接口
				this.pageLoading();
				this.Request('login', {
					account: this.userId,
					password: this.password
				}).then((res) => {
					// 验证通过
					if (res.error == 0) {
						canLogin = true;
						if (canLogin) {
							uni.reLaunch({
								url: '../../pagesLogin/pages/selectShop?from=login'
							})
						} else {
							this.idError = true; //账号密码不对
						}
					} else {
						this.Toast(res.message)
					}
					this.closePageLoading();
					requesting = false;
				}).catch(res => {
					requesting = false;
					this.Cacher.setData('login', {
						from: 'login'
					})
					if (res.error == -3) { //已登录
						uni.reLaunch({
							url: '../../pagesLogin/pages/selectShop?from=login'
						})
					} else {
						this.Toast(res.message)
					}
				})
			} else {
				setTimeout(() => {
					requesting = false;
					this.Toast('登录时间长，请重试')
				}, 3000)
			}
		},
		reg: function() { //找回密码
			uni.navigateTo({
				url: '../../pagesLogin/pages/index'
			})
		}
	}
}
